<script setup>
import { ref, onMounted } from 'vue'

const orders = ref([])

const getStatusVariant = (status) => {
  switch (status) {
    case 'Completed':
      return 'success'
    case 'Pending':
      return 'warning'
    case 'Processing':
      return 'info'
    case 'Cancelled':
      return 'danger'
    default:
      return 'secondary'
  }
}

